@mixin flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
$shadow: 3px 3px 8px 2px rgb(0,0,0, 0.5);

$body:  rgb(236,236,236);

body {
  background: $body;
  padding: 0 20px;
}

.add {
  color: white;
  font-weight: bold;
  font-size: 20px;
  padding: 5px 10px;
  border: none;
}

.header-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.header {
  span {
    font-size: 35px;
    color: red;
  }
}

.button-active {
  background: red;
}

.button-inactive {
  background: green;
}

.form {
  @include flex;
  margin: 50px 0;
  form {
    display: inherit;
    flex-direction: column;
    background: white;
    padding: 20px 40px;
    border-radius: 10px;
    box-shadow: $shadow;
    label {
      margin: 11px 0 6px 0;
      font-size: 15px;
      color: red;
      font-weight: bold;
    }
    input {
      width: 100%;
      padding: 4px;
    }
    .important-input {
      display: flex;
      align-items: center;
      margin-top: 10px;
      label {
        margin-right: 10px;
      }
    }

    button {
      width: 105%;
      background: black;
      color: white;
      font-weight: bold;
      padding: 5px 13px;
      border: none;
      margin-top: 10px;
      &:hover {
        background: #000000a4;
      }
    }
  }
}

.tasks {
  margin: 0 20px;
  margin-bottom: 100px;
}

.task {
  background: white;
  border-radius: 10px;
  box-shadow: $shadow;
  padding: 10px;
  margin-top: 40px;
  .important-text {
    color: red;
  }
  .completed-text {
    color: green;
  }
  .container {
    display: flex;
    align-items: center;
    .text {
      margin: 0 6px;
    }
  }
  .icons {
    float: right;
    @include flex;
    margin-top: 20px;
    margin-right: 15px;
  }
  .delete-btn, .complete-btn {
    background: red;
    padding: 4px;
    color: $body;
    cursor: pointer;
  }
  .complete-btn {
    background: green;
    margin-top: 10px;
  }
  h1 {
    font-size: 25px;
  }
  p {
    color: gray;
    span {
      color: green;
    }
  }

}

.completed-task {
  border-right: 5px green solid;
  opacity: 0.4;
}

.important-task {
  border-left: 5px red solid;
  background: rgba(255,0,0,0.05);
}


footer {
  @include flex;
  margin-top: 100px;
  p {
    font-size: 15px;
    font-weight: 500;
   .logo {
     color: red;
   }
  .icon-1, .icon-2 {
    font-size: 15px;
    color: #b80202;
  }
  
 .icon-2 {
   color: black;
 }
  }
}

#about {
  @include flex;
  color: black;
}

.about {
  padding: 20px;
}

.socials {
  margin-bottom: 50px;
 .social {
  .icon {
    font-size: 30px;
    margin-right: 15px;
    transition: all 0.3s ease-in-out;
    text-decoration: none;
    &:hover {
      opacity: 0.7;
      transform: translateY(-10px);
    }
  }
 }
}

@media (min-width: 688px) {
  .tasks {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
}